
import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import Navbar from '../components/Navbar';
import styles from '../../public/StyleSheet/train-status.module.css';
export default function TrainStatus() {
  const searchParams = useSearchParams();
  const initialTrain = searchParams.get('train') || '';

  const [trainInput, setTrainInput] = useState(initialTrain);
  const [trainSuggestions, setTrainSuggestions] = useState([]);
  const [trainStatus, setTrainStatus] = useState(null);
  const [trainList, setTrainList] = useState([]);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (initialTrain) handleSearch();
  }, []);

  const handleSearch = async () => {
    if (!trainInput.trim()) return;
    setLoading(true);
    try {
      const response = await fetch(
        `https://irctc1.p.rapidapi.com/api/v1/liveTrainStatus?trainNo=${trainInput}&startDay=1`,
        {
          method: 'GET',
          headers: {
            'x-rapidapi-key': `${process.env.NEXT_PUBLIC_RAPIDAPI_KEY}`,
            'x-rapidapi-host': 'irctc1.p.rapidapi.com',
          },
        }
      );

      const data = await response.json();

      if (!data.status || !data.data) {
        setError(data.message || 'Train not found');
        setTrainStatus(null);
      } else {
        const d = data.data;
        setTrainStatus({
          trainNumber: d.train_number,
          trainName: d.train_name,
          currentStation: d.current_station_name,
          eta: d.eta,
          etd: d.etd,
          platform: d.platform_number,
          delay: d.delay,
          statusAsOf: d.status_as_of,
          nextStopInfo: d.upcoming_stations?.[0]?.distance_from_current_station_txt || 'N/A',
        });
        setError(null);
      }
    } catch (err) {
      setError('Failed to fetch train status');
      setTrainStatus(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Navbar />
      <div className="max-w-4xl mx-auto py-12 px-4 sm:px-6 lg:px-8">
        <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
          <div className="bg-blue-600 px-6 py-8">
            <h2 className="text-3xl font-bold text-white">
              Live Train Status
            </h2>
            <p className="mt-2 text-blue-100">
              Track your train's real-time location and status
            </p>
          </div>

          <div className="p-6">
            <div className="space-y-4">
              <div className="relative">
                <input
                  type="text"
                  placeholder="Enter Train Number (e.g., 12345)"
                  className="w-full h-12 pl-4 pr-12 rounded-lg border-2 border-gray-200 focus:border-blue-500 focus:ring focus:ring-blue-200 transition-all duration-200"
                  value={trainInput}
                  onChange={(e) => setTrainInput(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
                />
                <button
                  onClick={handleSearch}
                  disabled={loading}
                  className="absolute right-2 top-2 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors duration-200 disabled:bg-blue-400"
                >
                  {loading ? 'Searching...' : 'Search'}
                </button>
              </div>

              {error && (
                <div className="p-4 bg-red-50 border-l-4 border-red-500 text-red-700">
                  {error}
                </div>
              )}

              {trainStatus && (
                <div className="mt-8 space-y-6">
                  <div className="bg-blue-50 border border-blue-100 rounded-xl p-6">
                    <div className="flex items-center justify-between mb-4">
                      <h3 className="text-xl font-semibold text-blue-900">
                        {trainStatus.trainName}
                      </h3>
                      <span className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm font-medium">
                        #{trainStatus.trainNumber}
                      </span>
                    </div>
                    
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <StatusItem 
                        label="Current Station"
                        value={trainStatus.currentStation}
                      />
                      <StatusItem 
                        label="Platform"
                        value={trainStatus.platform}
                      />
                      <StatusItem 
                        label="Expected Arrival"
                        value={trainStatus.eta}
                      />
                      <StatusItem 
                        label="Expected Departure"
                        value={trainStatus.etd}
                      />
                      <StatusItem 
                        label="Delay"
                        value={`${trainStatus.delay} minutes`}
                        isDelay={true}
                      />
                      <StatusItem 
                        label="Next Stop"
                        value={trainStatus.nextStopInfo}
                      />
                    </div>

                    <div className="mt-4 pt-4 border-t border-blue-100 text-sm text-blue-600">
                      Last updated: {trainStatus.statusAsOf}
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>

        {trainList.length > 0 && (
          <div className="mt-8">
            <h3 className="text-xl font-semibold text-gray-900 mb-4">
              Popular Trains
            </h3>
            <div className="bg-white rounded-xl shadow-md overflow-hidden">
              {trainList.map((train, index) => (
                <div
                  key={train.train_number}
                  className={`p-4 hover:bg-gray-50 cursor-pointer transition-colors duration-200 ${
                    index !== trainList.length - 1 ? 'border-b border-gray-100' : ''
                  }`}
                  onClick={() => setTrainInput(train.train_number)}
                >
                  <div className="flex justify-between items-center">
                    <div>
                      <h4 className="font-medium text-gray-900">{train.train_name}</h4>
                      <p className="text-sm text-gray-500">
                        {train.source} → {train.destination}
                      </p>
                    </div>
                    <span className="text-blue-600 font-medium">
                      #{train.train_number}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

const StatusItem = ({ label, value, isDelay }) => (
  <div className="flex flex-col">
    <span className="text-sm text-gray-500">{label}</span>
    <span className={`mt-1 font-medium ${isDelay && parseInt(value) > 0 ? 'text-red-600' : 'text-gray-900'}`}>
      {value}
    </span>
  </div>
);
